:root {
    --navigation-color: #202020;
    --navigation-color-transparent: rgba(15, 14, 14, 0.7);
    --navigation-color-secondary: #e07f7f;
    --navigation-color-tertiary: rgb(15, 15, 15);
    --text-color: #e2e2e2;
    --contact-color: #e26f2e;
    --contact-color-transparent: rgba(226, 111, 46, 0.7);
    --about-color: #3f48cc;
    --about-color-transparent: rgb(63, 72, 204, 0.7);
    --projects-color: #ea8aba;
    --projects-color-transparent: rgba(234, 138, 186, 0.7);
    --main-color: #193225;
    --main-color-transparent: rgba(25, 50, 37, 0.7);
}

* {
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    padding: 0;
    margin: 0;
}

body {
    margin: 0;
    padding: 0;
    background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),
        url("Images/background/nature.jpg");
    background-size: 100% 100%;
    color: var(--text-color);
}

.react-root,
html,
body {
    height: 100%;
    overflow-x: hidden;
}

.glass-box {
    padding: 20px 40px;
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border: 1px solid rgb(255, 255, 255, 0.1);
    border-radius: 20px;
    backdrop-filter: blur(15px);
}

.glass-box-container {
    height: 96%;
    width: 98%;
    margin: 1%;
    padding: 0;
}

.white-text {
    color: var(--text-color);
    font-size: larger;
}

.transparent-button {
    font-weight: bold;
    background-color: transparent;
    text-decoration: none;
    white-space: nowrap;
    border: 0;
    cursor: pointer;
}

/** Navigation Bar */
.navigation-bar {
    position: absolute;
    top: 0;
    background-color: var(--navigation-color-transparent);
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
    height: 6vh;
    width: 100%;
    padding-bottom: calc(20px + 1vh);
    z-index: 20;
    border: none;
    padding: 0;
    padding-bottom: 1vh;
    padding-left: 1vh;
    display: flex;
    align-items: stretch;
}

.navigation-logo {
    flex: 1;
}

.navigation-ul {
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.fill-container {
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 5vh;
}

.navigation-container {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    width: 20%;
    height: 100%;
}

.navigation-container:hover a {
    color: var(--navigation-color-secondary);
}

.burger {
    display: none;
}

.burger div {
    width: 25px;
    height: 3px;
    background-color: var(--text-color);
    margin: 3px;
    transition: all 0.3s ease;
}

.burgerToggle .line1 {
    transform: rotate(-45deg) translate(-6.25px, 6.25px);
}

.burgerToggle .line2 {
    opacity: 0;
}

.burgerToggle .line3 {
    transform: rotate(45deg) translate(-6.25px, -6.25px);
}

.login-button {
    width: 60%;
    height: 60%;
}

/** Page bar */
.page-bar {
    position: fixed;
    top: 5vh;
    width: 100%;
    height: 1vh;
    z-index: 21;
    background-color: var(--about-color);
}

/** Footer bar */
.bottom-bar {
    position: absolute;
    padding: 0;
    height: 6vh;
    width: 100%;
    background-color: var(--navigation-color-transparent);
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.bottom-bar ul {
    display: flex;
    height: 80%;
    width: 40%;
}

.bottom-bar ul li {
    list-style: none;
    height: 100%;
    width: 25%;
}

.bottom-bar ul li a {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.bottom-bar ul li a img {
    height: 100%;
}

.footer-links:hover {
    transform: scale(0.9);
    filter: invert(1);
    border-radius: 50px;
}

/* General CSS for every page */
/* General button */

.general-button {
    display: inline-block;
    font-size: 1.1em;
    color: #111;
    background: var(--text-color);
    padding: 10px;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 700;
    margin-top: 5px;
}

/* RGB button */
.rgb-button {
    position: relative;
    background: var(--navigation-color-transparent);
    border-radius: 50px;
    padding: 1px;
}

.rgb-button span {
    position: relative;
    display: inline-block;
    background-color: var(--navigation-color-tertiary);
    z-index: 1;
    height: 100%;
    width: 100%;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.rgb-button::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        45deg,
        #fb0094,
        #00f,
        #0f0,
        #ff0,
        #f00,
        #fb0094,
        #00f,
        #0f0,
        #ff0,
        #f00
    );
    opacity: 0;
    background-size: 400%;
    transition: 0.5s;
    filter: blur(4px);
    animation: animate_button 20s linear infinite;
    border-radius: 50px;
}

.rgb-button:hover::after {
    opacity: 1;
}

@keyframes animate_button {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 300% 0;
    }

    100% {
        background-position: 0 0;
    }
}

/* Background color of a glass box */
.background-glass-box .background-glass-box-child:nth-child(1),
.background-glass-box .background-glass-box-child:nth-child(2) {
    content: "";
    position: absolute;
    z-index: -1;
    top: -5%;
    left: 30%;
    width: 40%;
    height: 110%;
    border-radius: 8px;
    transform: skewX(15deg);
    transition: 0.5s;
}

.background-glass-box .background-glass-box-child:nth-child(2) {
    filter: blur(30px);
}

.background-glass-box:hover .background-glass-box-child:nth-child(1),
.background-glass-box:hover .background-glass-box-child:nth-child(2) {
    transform: skewX(0deg);
    top: -5%;
    left: -5%;
    width: 110%;
    height: 110%;
    border-radius: 20px;
}

/* Hovering span */
.hovering-span::after,
.hovering-span::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    opacity: 0;
    transition: 0.5s;
    animation: animateProjects 5s ease-in-out infinite;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    animation-delay: 1s;
}

.hovering-span::before {
    top: 0;
    left: 0;
}

.hovering-span-cover:hover .hovering-span::before {
    top: -50px;
    left: 50px;
    width: 100px;
    height: 100px;
    opacity: 1;
}

.hovering-span::after {
    bottom: 0;
    right: 0;
}

.hovering-span-cover:hover .hovering-span::after {
    bottom: -50px;
    right: 50px;
    width: 100px;
    height: 100px;
    opacity: 1;
}

@keyframes animateProjects {
    0%,
    100% {
        transform: translateY(50px);
    }

    50% {
        transform: translatey(-50px);
    }
}

/* The full container*/
.container {
    position: relative;
    top: 6vh;
    width: 100%;
    height: 84vh;
    overflow-y: auto;
    overflow-x: hidden;
    text-align: justify;
    padding: 20px;
    padding-left: 10%;
    padding-right: 10%;
}

.center-container-text {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.center-container-text h1 {
    font-size: 100px;
}

.center-container-text h2 {
    font-size: 40px;
}
/** Index page */

/** About page */

.container-about {
    padding-left: 20%;
    padding-right: 20%;
}

.about-information {
    margin-top: 5%;
    position: relative;
    width: 100%;
}

.about-information h2 {
    font-weight: 700;
    color: var(--text-color);
    font-size: 4em;
    line-height: 1.5em;
}

.about-information p {
    font-size: 24px;
    font-weight: 400;
    text-align: left;
}

.dad-joke-container {
    margin-top: 100px;
    margin-bottom: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.dad-joke-content {
    margin: 20px;
    text-align: justify;
    font-size: 20px;
}

.dad-joke-button {
    width: 80%;
    height: 40px;
    font-size: 30px;
    color: var(--text-color);
    text-align: center;
}

.dad-joke-link {
    text-decoration: none;
}

.content-about-programming {
    position: relative;
    padding: 0;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.programming-card {
    position: relative;
    width: 300px;
    height: 300px;
    text-align: center;
    z-index: 10;
    margin: 20px 20px 60px;
    transition: 0.5s;
}

.programming-card .box-about {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    border-radius: 10px;
}

.percent-about {
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    z-index: 100;
}

.percent-about .number {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.percent-about .number h2 {
    color: var(--text-color);
    font-weight: 700;
    font-size: 50px;
}

.percent-about .number h2 span {
    font-size: 24px;
}

.text {
    position: absolute;
    bottom: -42px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    padding: 30px 20px 10px;
    background: #f00;
    z-index: 1;
    color: var(--text-color);
    border-radius: 10px;
    font-weight: 800;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 1px;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
    pointer-events: none;
    transition: bottom 0.5s, z-index 0s, transform 0.5s, padding 0.5s;
    transition-delay: 0.5s, 0.5s, 0s, 0.5s;
}

.programming-card:hover .text {
    transition-delay: 0s, 0.5s, 0.5s, 0s;
    bottom: -70px;
    z-index: 11;
    transform: translateX(-50%) translateY(-50px);
    padding: 10px 20px 10px;
}

svg {
    position: relative;
    width: 150px;
    height: 150px;
}

svg circle {
    width: 100%;
    height: 100%;
    fill: none;
    stroke-width: 10;
    stroke: rgba(255, 255, 255, 0.05);
    transform: translate(5px, 5px);
    stroke-linecap: round;
}

svg circle:nth-child(2) {
    stroke: var(--text-color);
    stroke-dasharray: 440px;
    stroke-dashoffset: 440px;
}

.experiences {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-bottom: 10vh;
}

.experiences-box::before {
    content: "";
    position: absolute;
    top: 0;
    left: 20%;
    width: 60%;
    height: 100%;
    background: #3f48cc;
    border-radius: 8px;
    transition: 0.5s;
    transform: skewX(60deg);
    z-index: -1;
}

.experiences .experience-title::before {
    content: "";
    position: absolute;
    background: rgb(0, 153, 255);
    border-radius: 8px;
    transition: 0.5s;
    transform: skewX(60deg);
    z-index: -1;
    width: 200%;
    height: 100%;
    top: 0;
    left: -50%;
}

.experiences .experiences-box:hover::before {
    width: 400%;
    height: 120%;
    top: -10%;
    left: -200%;
}

.experiences .experiences-box {
    position: relative;
    width: 60%;
    height: fit-content;
    display: inline-block;

    text-align: center;
    padding: 10px;
    z-index: 5;
    margin: 20px;
}

.experiences .experiences-box .experience-content {
    position: relative;
    left: 0;
    z-index: 1;
    transition: 0.5s;
    color: var(--text-color);
}

.experiences .experiences-box .experience-content span::after,
.experiences .experiences-box .experience-content span::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    opacity: 0;
    transition: 0.5s;
    animation: animateSize 2s ease-in-out infinite;
    box-shadow: 0 5px 15px rgb(0 0 0 / 5%);
    animation-delay: 1s;
}

.experiences .experiences-box .experience-content span::after {
    animation: animateProjects 2s ease-in-out infinite;
}

.experiences .experiences-box:hover .experience-content span::after {
    top: -50px;
    left: -50px;
    width: 100px;
    height: 100px;
    opacity: 1;
}

.experiences .experiences-box .experience-content:hover span::before {
    top: 0;
    left: 90%;
    width: 100px;
    height: 100px;
    opacity: 1;
}

/** Projects page */
.projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.project-box {
    position: relative;
    width: 400px;
    height: 400px;
    margin: 40px 30px;
    transition: 0.5s;
}

.project-box div {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.content-card {
    position: relative;
    left: 0;
    z-index: 1;
    transition: 0.5s;
    color: var(--text-color);
}

.content-card:hover {
    left: -25px;
    padding: 60px 40px;
}

.content-card h2 {
    font-size: 2em;
    color: var(--text-color);
    margin-bottom: 10px;
}

.content-card p {
    word-wrap: break-word;
    font-size: 18px;
    line-height: 1.6;
    font-weight: 400;
    text-decoration: none;
    font-style: normal;
    letter-spacing: 0;
    color: #ffe5e5;
    margin-bottom: 30px;
}

.content-card img {
    position: relative;
    width: 100%;
    height: 25%;
    border-radius: 10px;
    padding: -1px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border: 2px solid black;
    background: black;
}

.content-card h4 {
    font-size: 1.1em;
    margin-bottom: 10px;
}

/* Other Screen Sizes */
@media screen and (max-width: 768px) {
    .navigation-bar {
        height: 5vh;
        padding-bottom: 0;
    }

    .container {
        height: 85vh;
    }

    .navigation-ul {
        position: absolute;
        right: 0;
        height: 85vh;
        width: 0;
        top: 5vh;
        background-color: var(--navigation-color-transparent);
        flex-direction: column-reverse;
        transform: translateX(100%);
        transition: transform 0.3s ease-in;
        box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
        backdrop-filter: blur(15px);
    }

    .navigation-ul li {
        width: 100%;
        height: 19%;
    }

    .burger {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 20%;
        height: 100%;
    }

    .page-bar {
        right: 0px;
        width: 0px;
        height: 85vh;
        transform: translateX(99%);
        transition: 0.3s;
        box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
        z-index: 10;
        backdrop-filter: blur(15px);
    }
}

/** All actives */
.nav-active-bar {
    width: 61%;
    transform: translateX(0%);
}

.nav-active {
    width: 60%;
    transform: translateX(0%);
}
